// ‰ΩøÁî® localStorage Êù•‰øùÂ≠ò IP ‰ø°ÊÅØ
let ipLocation = null;

function fetchIPInfo() {
  // Ê£ÄÊü• localStorage ‰∏≠ÊòØÂê¶Â∑≤ÊúâÊï∞ÊçÆÔºå‰∏îÊï∞ÊçÆÊú™ËøáÊúü
  const savedData = localStorage.getItem('ipLocationData');
  const savedTimestamp = localStorage.getItem('ipLocationTimestamp');
  const now = new Date().getTime();

  // Â¶ÇÊûúÊúâ‰øùÂ≠òÁöÑÊï∞ÊçÆ‰∏îÊú™Ë∂ÖËøá1Â∞èÊó∂ÔºåÁõ¥Êé•‰ΩøÁî®
  if (savedData && savedTimestamp && (now - parseInt(savedTimestamp) < 3600000)) {
    ipLocation = JSON.parse(savedData);
    showWelcome();
    return;
  }

  // Âê¶ÂàôÈáçÊñ∞ËØ∑Ê±Ç
  fetch('https://api.nsmao.net/api/ip/query?key=YJLRvE0u5W1hww4uuOVnlzdeQ4')
      .then(response => {
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        return response.json();
      })
      .then(data => {
        ipLocation = data;
        if (ipLocation.code === 500) {
          throw new Error('Failed to obtain location information');
        }
        // ‰øùÂ≠òÂà∞ localStorageÔºåÂêåÊó∂‰øùÂ≠òÊó∂Èó¥Êà≥
        localStorage.setItem('ipLocationData', JSON.stringify(data));
        localStorage.setItem('ipLocationTimestamp', new Date().getTime().toString());
        showWelcome();
      })
      .catch(error => {
        ipLocation = {
          data: {
            country: 'Á•ûÁßòÂú∞Âå∫',
            prov: 'Á•ûÁßòÂú∞Âå∫',
            city: '',
            district: '',
            lat: 0,
            lng: 0
          },
          ip: ''
        };

        // Â¶ÇÊûúÊúâ‰øùÂ≠òÁöÑÊï∞ÊçÆÔºåÂ∞ùËØï‰ΩøÁî®‰øùÂ≠òÁöÑÊï∞ÊçÆ
        if (savedData) {
          try {
            ipLocation = JSON.parse(savedData);
          } catch (e) {}
        }
        showWelcome();
      });
}

function getDistance(e1, n1, e2, n2) {
  // Â¶ÇÊûúÊòØÁ•ûÁßòÂú∞Âå∫ÔºåÁõ¥Êé•ËøîÂõû'N'
  if (e2 === 0 && n2 === 0) {
    return 'N';
  }

  const R = 6371;
  const { sin, cos, asin, PI, hypot } = Math;
  let getPoint = (e, n) => {
    e *= PI / 180;
    n *= PI / 180;
    return { x: cos(n) * cos(e), y: cos(n) * sin(e), z: sin(n) };
  };

  let a = getPoint(e1, n1);
  let b = getPoint(e2, n2);
  let c = hypot(a.x - b.x, a.y - b.y, a.z - b.z);
  let r = asin(c / 2) * 2 * R;
  return Math.round(r);
}

